@import '../commons.scss';

.lander {
    padding: 40px 20px;

    .lander-heading {
        text-align: center;
        margin-top: 40px;
    }

    .poison {
        text-align: center;
        margin: 40px;
    }

    .links-container {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;

        .link {
            display: block;
            width: 100%;
            max-width: 250px;
            color: white;
            text-align: center;
            padding: 20px 20px;
            margin: 10px 0;
            text-transform: uppercase;
            text-decoration: none;
        }

        .quickfire {
            background-color: $blue;
        }

        .karaoke {
            background-color: $red;
        }

        .info {
            background-color: $green;
        }
    }
}



