@import '../../commons.scss';

.time-selector-container {
    display: none;
}

.time-selector-container.visible {
    background-color: white;
    position: absolute;
    top: 50vh;
    left: 50vw;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-box-shadow: 0px 0px 100px 10px black;
            box-shadow: 0px 0px 100px 10px black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: hidden;
    padding: 10px;

    .close-btn, .time-btn {
        cursor: pointer;
        border: none;
        padding: 10px;
        margin: 3px;
        color: white;
        font-size: 1.2rem;
    }

    .time-btn {
        width: 200px;
        background-color: $blue;
    }

    .close-btn {
        background-color: $red;
    }
}



